@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
margin: 0;
font-family: "Quicksand";
color: #333;
}
.container {
  margin: 1rem;
  text-align: center;
}
.nav {
padding: 20px;
display: flex;
justify-content: space-between;
align-items: center;
max-width: 800px;
margin: 0 auto;
border-bottom: 1px solid #f2f2f2;
}
.navbar-logo {
color: #98bcfa;
font-size: 40px;
}
.navbar-menu {
padding: 0;
margin: 0;
list-style: none;
display: flex;
gap: 1.5rem;
}
.nav a {
text-decoration: none;
}
.nav a:hover {
color: #98bcfa;
}